.button-primary {
  background-color: var(--primary-main-color);
  color: var(--secondary-sub-color);
  border: none;
  padding: 10px 20px;
  border-radius: 40px;
  cursor: pointer;
  text-decoration: none;
}

.login_font {
  color: var(--secondary-main-color);
  font-size: 14px;
}

.success_font {
  font-size: 24px;
  color: var(--primary-main-color);
  font-weight: bold;
}

.nav_title {
  font-size: 20px;
  font-weight: bold;
}


.navbar-wrapper {
  width: 90%;
}

@media (max-width: 855px) {
  .navbar-wrapper {
    width: 100%;
  }
}

.form_styling {
  box-shadow: 0px 4px 10px rgba(4,4,4,0.3);
  padding: 20px;
  background-color: var(--secondary-sub-color);
}

.signin_btn{
  border: none;
  padding: 3px 7px;
  border-radius: 5px;
  background-color: var( --primary-main-color);
  color: var(--secondary-sub-color);
}

.search-input {
  width: 450px !important;
  transition: width 0.3s ease-in-out;
}

@media (max-width: 1024px) {
  .search-input {
    width: 230px !important;
  }
}

@media (max-width: 768px) {
  .search-input {
    width: 180px !important;
  }
  .offcanvas.offcanvas-end{
    width: 300px !important;
  }
}

.search_top{
  background-color:rgba(243, 243, 243);
}

.custom_modal{
  border-radius: 0px !important;
}

.city_modal{
 padding: 8px 10px;
 border-radius:20px;
 cursor: pointer;
 border: 1px solid var( --primary-main-color);
}

.form-control:focus{
 border-color: #dee2e6 !important;
}

.navbar_bg{
  background: var(--secondary-sub-color);
}